@font-face {
  font-family: Zenloop;
  src:  url(/businesswebsite/static/media/ZenLoop.8a312b30.ttf);
}

html {
  background: #9173AB;
}
.app {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.Navigation {
	display: flex;
	width: 95vw;
	justify-content: space-between;
	border: 2px solid black;
	border-top: none;
	position: -webkit-sticky;
	position: sticky;
	border-radius: 0px 0px 50px 50px;
	background: #F0DEFF;
	margin: -8px auto 15px auto;
	text-shadow: 0px 0px 1px #410773;
}
.subnav {
	display: flex;
	align-items: center;
	padding-left: 20px;
	padding-right: 20px;
}
button{
	padding:  0px 6px 0px 6px;
	text-decoration: none;
	text-shadow: 0px 0px 2px white;
	background: none;
	border: none;
	font-family: Zenloop;
	color: #410773;
	letter-spacing: -1px;
	font-weight: 1000;
	font-size: 28px;
	cursor: pointer;
	text-shadow: 0px 0px 1px #410773;
}


@media screen and (max-width: 710px) {
	button {
		font-size: 25px;
	}
}
.Heading {
	justify-self: center;
	display: flex;
	justify-content: center;
	box-shadow: 0px 0px 10px black inset;
	border: 2px solid black;
	flex-basis: 100%;
	background: #F0DEFF;

}
.Title-text {
	font-size: 10vh;
	font-family: ZenLoop;
	color:  #9173AB;
	text-shadow: -15px 15px 15px black,
				-1px -1px 1px black,
				-2px -1px 1px black,
				-3px -2px 1px black,
				-4px -2px 1px black,
				-5px -3px 1px black,
				-6px -3px 1px black;

}

@media screen and (max-width: 710px) {
.Title-text {
	font-size: 6vh;
}
}
.home {
	min-height: 45vh;
}

.mission-statement {
	display: flex;
	align-items: center;
	margin: 20px;
	max-height: 60vh;
	background: rgba(255, 255, 255, 0.2);
	padding: 10px;
	border: 2px solid black;
	border-radius: 50px;
}

h1 {
	font-family: ZenLoop;
	letter-spacing: 2px;
	font-size: 40px;
	cursor: default;
}

img {
	width: 300px;
	border: 3px solid black;
	border-radius: 150px;
	margin: 10px;
	box-shadow: 0px 0px 10px black;
}
.brief {
	display: flex;
	align-items: center;
	text-shadow: 0px 0px 1px #410773;
	font-weight: 1000;
	margin: 60px;
	height: 18vh;
	font-family: Zenloop;
	color: #410773;
	font-size: 28px;
}

@media screen and (max-width: 710px) {
	.mission-statement {
		height: 40vh;
	}
	h1 {
		font-size: 33px;
	}
	img {
		width:  200px;
	}
} 

.footer {
	display: flex;
	flex-direction: column;
	height: 300px;
	border-top: 3px solid black;
	background: rgba(0, 0, 0, 0.95);
	width:  101vw;
	margin:  20px 0px -12px -9px;
}
.text-links {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.text-link {
	margin: 3px;
	display: flex;
	text-shadow: none;
	color: #F0DEFF;
	font-size: 20px;
	transition: all .2s;
}
.img-links {
	margin-top: 10px;
	display: flex;
	height: 120px;
	justify-content: center;
	border: none;
}
.img-link {
	margin: 15px;
	height: 80px;
	width: auto;
	border: none;
	transition: all .2s;
}

.text-link:hover, .img-link:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
	text-shadow: 0px 0px 10px purple;
}
.img-link:hover{
	box-shadow: 0px 0px 10px purple;
}
.user-fields {
	display: flex;
	justify-content: center;
	align-items: center;	
	min-height: 44vh;
	font-family: ZenLoop;
	font-weight: 1000;
	color: #410773;
	text-shadow: 0px 0px 1px solid #410773;	
	font-size: 30px;
}
.signin-fields {
	display: flex;
	flex-direction: column;
	align-items: center;
	height: 10vh;
	padding: 10px;
	width: 400px;
}

.register-fields {
	display: flex;
	flex-direction: column;
	align-items: center;
	height: 8vh;
	padding: 5px;
	width: 200px;

}

label {
	margin: 10px;
	text-decoration: underline;
}
input {
	height: 30px;
	border: 2px solid #410773;
	box-shadow: 0px 0px 5px #410773;
}
fieldset {
	display: flex;
	flex-direction: column;
	border: 1px solid #410773;
	box-shadow: 0px 0px 5px #410773 inset ;
	max-width: 600px;
}
legend {
	text-shadow: 0px 0px 5px #743AA6;
	text-decoration: underline;
}

.user-info {
	display: flex;
	flex-wrap: wrap;

	justify-content: space-around;
}
.registration-info {
	display: flex;
	justify-content: space-around;
}

h3 {
	text-align: center;
	text-decoration: underline;
}

button.submit {
	align-self: center;
	border: 2px solid #410773;
}

.submit-button {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 10px;
}
.about {
	display: flex;
	min-height: 45vh;
	flex-direction: column;	
}
.intro, h2 {
	align-self: center;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 80vw;
	height: 10vh;
	font-size: 60px;
	border-radius: 50px;
	background: rgba(255, 255, 255, 0.2);
	box-shadow: 0px 0px 10px black;
	color: #410773;
	text-shadow: 0px 0px 1px #410773;
}

h2 {
	font-size: 40px;
	font-family: ZenLoop;
}

.information {
	font-size: 18px;
	font-family: helvetica;
	margin: 20px;
}
.profile, .service {
	display: flex;
	margin: 40px;
	align-items: center;
	background: rgba(255, 255, 255, 0.1);
	border-radius: 20px;
	padding: 10px;
	box-shadow: 0px 0px 5px black;
}

.contact {
	display: flex;
	flex-direction: column;
	align-items: center;
	height:  45vh;
	font-family: ZenLoop;
}
.contact-head {
	margin: 20px;
	display: flex;
	color: #410773;
	text-decoration: underline;
	text-underline-position: under;
	text-shadow: 0px 0px 1px #410773;
	background: rgba(255, 255, 255, 0.2);
	width: 400px;
	justify-content: center;
	height: 100px;
	border-radius: 20px;
	box-shadow: 0px 0px 5px black;
}
.contact-methods {
	display: flex;
	flex-direction: column;
	width: 30vw;
}
.contact-info {
	display: flex;
	justify-content: space-between;
	border-bottom: 1px solid black;
	font-size: 23px;
	font-weight: 1000;
	color:  #410773;
	text-shadow: 0px 0px 1px #410773;
	letter-spacing: 1px;
}
